body {
    margin: 0;
    font-family: 'Courier New', Courier, monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #000;
    color: #00ff00;
  }
  
  #root {
    height: 100vh;
    overflow: auto;
    padding: 20px;
    box-sizing: border-box;
  }
  
  input {
    font-family: 'Courier New', Courier, monospace;
    font-size: 16px;
    background-color: transparent;
    border: none;
    color: #00ff00;
    outline: none;
  }
  
  input::placeholder {
    color: #00ff00;
    opacity: 0.5;
  }
  
  .terminal-output {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  
  .prompt {
    display: flex;
    align-items: center;
  }
  
  .prompt span {
    margin-right: 8px;
  }